.place-info {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  &__fav-btn {
    position: absolute;
    top: .1rem;
    left: -1rem;
    font-size: 1.5em;
    background: none;
    border: none;
    color: #ff9800;
    cursor: pointer; }
  &__base-info {
    padding: 0 2rem;
    display: flex;
    flex-direction: column; }
  &__desc {
    text-align: justify; }
  &__main-img {
    height: 100% !important;
    object-fit: cover; }
  &__swiper {
    margin-top: auto;
    max-height: 300px; }
  .swiper-wrapper {
    display: flex;
    align-items: flex-end;
    margin-top: 2rem; }
  &__video {
    width: 100%; } }
